.accountSettingsHeader {
  padding: 0.5rem 0.9rem !important;
}
.wrapperScroll {
  overflow: hidden;
}
.pageOuterSection {
  height: auto;
}
.sectionHeader {
  height: 104px;
  color: rgba(0,0,0,0.87);
  font-weight: 500;
  font-size: 24px;
  line-height: 17px;
  align-items: center;
  color: rgba(0, 0, 0, 0.87);
  font-weight: 500; 
  align-items: center;
  padding: 0 5rem;  
  span {
    font-size: 16px;
    color: #868686;
    margin-left: 0.5rem;
  }
}
.sidebarLeftCol {
  width: 300px;
  height: calc(100vh - 80px);
  display: flex;
  padding: 4.7rem 0 0 0  !important;
  flex-direction: column;
  left: 0;
  position: -webkit-sticky;
  position: sticky;
  padding: 30px 15px 15px;
  z-index: 1;
  .settingNavLinkBtn {
    font-size: 0.9rem;
    text-align: left !important;
    font-weight: 400 !important;
    padding: 1.1rem 1.4rem;
    border-right: 4px solid #fff;
    background-color: #fff;
    font-style: normal;
    font-weight: normal;
    font-size: 1.1rem;
    line-height: 17px;
    border-color: #fff;
    color: #000000;
    &.disabled,
    &:disabled {
      background-color: #F7F7F7;
      border-right: 4px solid #478CF9;
      opacity: 1;
      color: #3196F2;
    }
    &:active {
      border-color: #F7F7F7 !important;
    }
    &:hover, &:active {
      border-right: 4px solid #478CF9;
      background-color: #F7F7F7 !important;
      color: #3196F2 !important;
    }
    .navIcons {
      width: 30px;
      padding-right: 5px;
      position: relative;
      top: -2px;
      img {
        width: 18px;
        
      }
    }
  }
  
}
