.floorPlanChart {
    padding-top: 1.5rem !important;
    position: relative;
    left: -5px;
    .recharts-wrapper {
        width: 460px !important;
    }
    .recharts-default-legend {
        padding-top: 1rem !important;
    }
}

@media only screen and (min-width: 1200px) and (max-width: 1280px) {
    .floorPlanChart {
        .recharts-wrapper {
            overflow-x: scroll;
            overflow-y: hidden;
        }
    }
}

@media only screen and (max-width: 1024px) {
    .floorPlanChart {
        .recharts-wrapper {
            overflow-x: scroll;
            overflow-y: hidden;
        }
    }
}
