@import "../../../assets/scss/variables.scss";

.selectedProject {
  background-color: $table-hover-bg !important;
}

.projectListHeader {
  z-index: 10;
}

.badge {
  line-height: 12px;
  padding: 0.25em 0.8em;
  border-radius: $border-radius-xlg;
}

.folderIcon {
  position: relative;
  > div {
    font-size: 0;
    height: 34px;
    width: 34px;
    justify-content: center;
    align-items: center;
    position: relative;
    display: flex;
    &:before {
      content: "";
      position: absolute;
      z-index: -1;
      bottom: 0px;
      left: 0px;
      right: 0px;
      top: 0px;
      border-radius: $rounded-pill;
      background-color: $gray-200;
    }
  }
}

.statusIconImg {
  img {
    width: 22px;
  }
}
