@import "../../../assets/scss/variables.scss";

.chargeCodeHeader {
  margin: 0;
  div {
    padding: 0.6rem 0.3rem;
    white-space: nowrap;
  }
  .widthId {
    width: 25px;
  }
  .widthChargeCode {
    width: 140px;
  }
  .widthTotalAmount {
    width: 100px;
  }
}

.chargeCode {
  overflow: auto;
  height: calc(100vh - 302px);
  width: 97.4%;
  padding-right: 0px;
  flex-grow: 0;
  flex-basis: auto;
  .table {
    margin-bottom: 0px;
  }
  ul {
    margin-bottom: 0px;
    width: 100%;
    padding: 0.4em 0.3rem;
    display: block;
    border-bottom: (1 * $table-border-width) solid $table-border-color;
    position: relative;
    li {
      padding: 0;
      font-size: $font-size-xsmall;
      vertical-align: middle;
      border-top: none;
      background-color: $white;
      display: inline-flex;
      width: auto;
    }
    .widthId {
      width: 25px;
    }
    .widthChargeCode {
      width: 138px;
    }
    .widthTotalAmount {
      width: 100px;
    }
    .widthChargeCodeOption {
      width: 158px;
      display: inline-block;
    }
    .dottedIcon {
      position: absolute;
      right: 5px;
      top: 10px;
      cursor: pointer;
      div {
        padding: 0;
      }
    }
    .overlayView {
      position: absolute;
      width: 100%;
      top: 0;
      background-color: $light-blue;
      height: 100%;
      padding: 0;
      left: 0;
      div {
        font-size: $font-size-xsmall !important;
      }
    }
    .overlayViewInputs {
      display: flex;
      align-items: center;
      justify-content: space-around;
      flex-direction: row;
      border: none;
      padding: 0;
      li {
        display: inline-flex;
        background: inherit;
      }
      .overlayViewOptions {
        display: inline-block !important;
        width: 133px;
        margin: 0 3px;
      }
      .overlayViewButton {
        margin: 0 4px;
        width: 36px;
        border: (1 * $table-border-width) solid $table-border-color;
        height: 33px;
      }
      .closeIcon {
        img {
          width: 12px;
        }
      }
      .textDesciption {
        text-align: center;
        justify-content: center;
        font-size: $font-size-small;
      }
    }
  }
}

.chargeCodeFooter {
  > :not(:last-child) {
    margin-right: 0.55rem;
  }
}

.appContextMenu > div > {
  :nth-child(2) {
    right: 252px !important;
    left: auto !important;
    margin-top: 10px;
  }
}

.widthChargeCodeDotted {
  width: 98%;
}

@media only screen and (min-width: 1600px) and (max-width: 1680px) {
  .chargeCodeHeader {
    .widthChargeCode {
      width: 162px;
    }
  }

  .chargeCode {
    ul {
      .widthChargeCode {
        width: 162px;
      }
    }
  }
}

@media only screen and (max-width: 1440px) {
  .chargeCodeHeader {
    .widthChargeCode {
      width: 150px;
    }
  }

  .chargeCode {
    ul {
      .widthChargeCode {
        width: 150px;
      }
    }
  }
}

@media only screen and (min-width: 1360px) and (max-width: 1366px) {
  .chargeCodeHeader {
    .widthChargeCode {
      width: 140px;
    }
  }

  .chargeCode {
    ul {
      .widthChargeCode {
        width: 138px;
      }
    }
  }
}

@media only screen and (min-width: 1200px) and (max-width: 1280px) {
  .chargeCodeHeader {
    .widthChargeCode {
      width: 132px;
    }
  }

  .chargeCode {
    ul {
      .widthChargeCode {
        width: 132px;
      }
    }
  }
}
