.occupancyHeader {
  font-size: 13px;
  font-weight: 600;
  margin: 0;
  background-color: #e5eeff;
  border-bottom: 1px solid #c0d7ea;
  position: relative;
  border-top-left-radius: 0.3rem;
  border-top-right-radius: 0.3rem;
  div {
    padding: 0.3rem 0.4rem;
    white-space: nowrap;
  }
  .documentStatusHeader {
    width: 140px;
  }
  .unitHeader {
    width: 80px;
  }
  .sqFtHeader {
    width: 110px;
    small {
      display: block;
      color: #007bfe;
    }
  }
  .percentHeader {
    width: 130px;
  }
  .categoryHeader {
    width: 115px;
  }
}

.occupancyWrapper {
  width: 97%;
  height: calc(100vh - 286px);
  min-height: 100px;
  overflow-y: scroll;
  overflow-x: hidden;
  padding-right: 0px;
  flex-grow: 0;
  flex-basis: auto;
  .table {
    margin-bottom: 0px;
  }
  table {
    margin-bottom: 0px;
    td {
      padding: 0.3rem 0.4em;
      font-size: 13px;
      width: auto;
      vertical-align: middle;
      border-top: none;
      background-color: #ffffff;
      border-bottom: 1px solid #dee2e6;
    }
    .documentStatus {
      width: 140px;
    }
    .units {
      width: 80px;
    }
    .sqFt {
      width: 110px;
      small {
        display: block;
        color: #007bfe;
      }
    }
    .percent {
      width: 133px;
    }
    .categroy {
      width: 115px;
    }
    .columnNowrapCode {
      width: 133px;
    }
    .columnNowrapUnits {
      width: 69px;
    }
    .columnNowrapAvgsqft {
      width: 99px;
    }
  }
}

.occupancyStatusChart {
  font-size: 13px;
  padding: 1rem 0 4rem;
}

.occupancyRRWrapper {
  height: calc(100vh - 268px);
  width: 99.8%;
  overflow-y: scroll;
  overflow-x: hidden;
  margin-top: 0.3rem;
}

.cardsFixBottom {
  position: relative;
}

.footerFixedBottom {
  margin: 0.7rem 0 0;
  bottom: 0px;
  justify-content: flex-end;
  padding: 0.5rem 1rem;
  background-color: #ffffff;
  box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.5);
  > :not(:last-child) {
    margin-right: 0.55rem;
  }
}

.footerFixedBottom {
  position: sticky;
  width: 100%;
  bottom: 0px;
}
@media only screen and (max-width: 1920px) {
  .occupancyHeader {
    .documentStatusHeader {
      width: 150px;
    }
    .unitHeader {
      width: 85px;
    }
  }
  .occupancyWrapper {
    height: calc(100vh - 725px);
    table {
      .documentStatus {
        width: 148px;
      }
    }
  }
}
@media only screen and (min-width: 1600px) and (max-width: 1680px) {
  .occupancyHeader {
    .documentStatusHeader {
      width: 146px;
    }
  }
  .occupancyWrapper {
    height: calc(100vh - 725px);
    table {
      .documentStatus {
        width: 146px;
      }
    }
  }
}

@media only screen and (max-width: 1440px) {
  .occupancyHeader {
    .documentStatusHeader {
      width: 140px;
    }
  }
  .occupancyWrapper {
    height: calc(100vh - 725px);
    table {      
      .documentStatus {
        width: 140px;
      }
      .percent {
        width: 70px;
      }
    }
  }
}

@media only screen and (min-width: 1360px) and (max-width: 1366px) {
  .occupancyHeader {
    .documentStatusHeader {
      width: 135px;
    }
  }
  .occupancyWrapper {
    height: calc(100vh - 730px);
    table {
      .documentStatus {
        width: 137px;
      }
      .percent {
        width: 64px;
      }
    }
  }
}

@media only screen and (min-width: 1200px) and (max-width: 1280px) {
  .occupancyHeader {
    .documentStatusHeader {
      width: 148px;
    }
    .unitHeader {
      width: 82px;
    }
    .categoryHeader {
      width: 102px;
    }
  }
  .occupancyWrapper {
    height: calc(100vh - 650px);
    table {
      .documentStatus {
        width: 155px;
      }
      .category {
        width: 107px;
      }
    }
  }
}

