@import "../../../assets/scss/variables.scss";

.floorPlanHeader {
  margin: 0;
  height: auto;
  div {
    padding: 0.5rem 0.2rem;
    white-space: nowrap;
    small {
      display: block;
      color: $primary;
    }
  }
  .fPlan {
    width: 114px;
  }
  .floorPlanWidth {
    width: 60px !important;
  }
  .avgsqft {
    width: 76px;
  }
  .count {
    width: 79px;
  }
  .marketrent {
    width: 117px;
  }
  .monthlyrent {
    width: 120px;
  }
  .beds {
    width: 170px;
    margin-left: 45px;
  }
}

.floorPlan {
  overflow: auto;
  height: calc(100vh - 362px);
  width: 97%;
  padding-right: 0px;
  flex-grow: 0;
  flex-basis: auto;
  .table {
    margin-bottom: 0px;
  }
  table {
    margin-bottom: 0px;
    td {
      padding: 0.4em 0.2rem;
      font-size: $font-size-xsmall;
      width: auto;
      line-height: 16px;
      vertical-align: middle;
      border-top: none;
      background-color: $white;
      border-bottom: (1 * $table-border-width) solid $table-border-color;
      height: auto;
    }
    .fPlan,
    .avgsqft,
    .count,
    .marketrent,
    .monthlyrent {
      small {
        color: $primary;
        display: block;
      }
    }
    .fPlan {
      width: 46px;
      small {
        text-decoration: underline;
        cursor: pointer;
      }
    }
    .avgsqft {
      width: 74px;
    }
    .count {
      width: 66px;
    }
    .marketrent {
      width: 108px;
    }
    .monthlyrent {
      width: 110px;
    }
    .beds {
      width: 80px;
    }
    .bath {
      width: 80px;
    }
  }
}
.overflowPadding {
  padding-right: 30px;
}

.floorPlanFooter {
  > :not(:last-child) {
    margin-right: 0.55rem;
  }
}
.pt8 {
  padding-top: 8px;
}

.floorPlanChartWrapper {
  overflow: hidden;
  overflow-y: scroll;
  height: calc(100vh - 313px);
  width: 100%;
  margin: 0 auto;
  padding-right: 0px;
  flex-grow: 0;
  padding: 1rem 0 0 1rem;
  flex-basis: auto;
  font-size: $font-size-msmall;
  svg {
    font-size: $font-size-msmall;
  }
}

.floorPlanChartWrapperBx {
  height: 250px;
  margin: 1.5rem 0 0 0;
}

@media only screen and (max-width: 1920px) {
  .floorPlanHeader {
    .avgsqft {
      width: 85px;
    }
    .marketrent {
      width: 130px;
    }
    .monthlyrent {
      width: 129px;
    }
    .count {
      width: 75px;
    }
    .beds {
      margin-left: 58px;
    }
  }
}

