  .floorPlanHeader {
    .avgsqft {
      width: 82px;
    }
    .marketrent {
      width: 125px;
    }
    .monthlyrent {
      width: 128px;
    }
    .count {
      width: 75px;
    }
    .beds {
      margin-left: 56px;
    }
  }
}

@media only screen and (max-width: 1440px) {
  .floorPlanHeader {
    .avgsqft {
      width: 80px;
    }
    .marketrent {
      width: 122px;
    }
    .monthlyrent {
      width: 123px;
    }
    .count {
      width: 77px;
    }
    .beds {
      margin-left: 54px;
    }
  }
}

@media only screen and (min-width: 1360px) and (max-width: 1366px) {
  .floorPlanHeader {
    .avgsqft {
      width: 78px;
    }
    .marketrent {
      width: 118px;
    }
    .monthlyrent {
      width: 124px;
    }
    .count {
      width: 72px;
    }
    .beds {
      margin-left: 52px;
    }
  }
}

@media only screen and (min-width: 1200px) and (max-width: 1280px) {
  .floorPlanHeader {
    .avgsqft {
      width: 75px;
    }
    .marketrent {
      width: 118px;
    }
    .monthlyrent {
      width: 122px;
    }
    .count {
      width: 72px;
    }
  }
}
