// formula bar
.formulaBarInputContainer {
  background-color: $white;
  border-bottom: 1px solid #c0c0c0;
  height: 1.7rem;
  padding: 0;
  position: relative;
}
.formulaBarLabel {
  text-align: center;
  width: 120px;
  height: 1.6rem;
  line-height: 1.6rem;
  display: inline-block;
  padding: 1px 8px;
  font-weight: 600;
  background-color: #e5e8ec;
}
.formulaBar {
  background-image: url(../../../assets/images/fx.svg);
  background-repeat: no-repeat;
  background-size: 15px;
  background-position: 0px center;
  padding-left: 36px;
  height: 1.6rem;
  line-height: 1.6rem;
  opacity: 0.7;
}
.formulaBarInput {
  border: none;
  position: relative;
}
.cellInput {
  word-wrap: break-word;
  width: 100%;
  height: 100%;
  margin: 0;
  outline: none;
  cursor: text;
  border: none;
  white-space: pre-wrap;
  background-color: $white;
  color: rgba(0, 0, 0, 0.7);
}

// menu dropdwon
.spreadsheetToolbarDropdownButton {
  button {
    font-size: 14px;
    font-weight: 700;
    color: rgba(0, 0, 0, 0.7) !important;
    background-color: $white;
    border: none;
    box-shadow: none;
    outline: none;
    cursor: pointer;
    border-radius: 3px;
    &:hover,
    &:focus,
    &:active {
      background-color: #f1f3f5 !important;
      border-color: $light-border !important;
      border: none;
      box-shadow: none !important;
      outline: none !important;
    }
  }
}
