@import "../../../assets/scss/variables.scss";

.linkedInButtonWrapper {
  height: 50px;
  width: 240px;
  background-color: #007bb6 !important;
  color: $white !important;
  padding: 1px;

  .linkedInIconWrapper {
    width: 48px;
    height: 48px;
    line-height: 36px;
    padding: 4px;
    background-color: $white;
    border-radius: $btn-border-radius !important;

    .linkedInLogo {
      width: 18px;
      height: 18px;
    }
  }

  .linkedInTextWrapper {
    font-size: $font-size-base;
    line-height: 48px;
  }
}
