@import "../../../assets/scss/variables.scss";

.headerFilter div {
    display: inline-block;
    margin-left: 0.3rem;
}
.headerFilter div select {
    font-size: $font-size-msmall;
}

.headerFilter div.autocompleteItem {
    display: block;
    margin: 0;
    padding: 3px 7px;
}

.projectsListViewWrapper {
    .table {
        tbody td {
            > div > div {
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
            }
        }
    }
    .headerFilter {
        .form-control {
            min-width: 155px;
            padding: $input-btn-padding-y;
        }
        &.assetTypes {
            .form-control {
                padding: $input-btn-padding-y 0.75rem $input-btn-padding-y 0.1rem;
            }
        }
        &.inputPlaceholder {
            > div input,
            > div > div input {
                &::placeholder {
                    color: $input-color !important;
                    font-style: normal !important;
                }
            }
        }
    }
}
