// input group content center end
.input-group-content-center-end {
  .form-control {
    padding-right: $spacer * 4.5 !important;
  }
  .input-group {
    > .form-control {
      width: 100%;
      flex: none;
    }
  }
  .input-group-append {
    width: auto;
    position: absolute;
    z-index: 5;
    top: 0rem;
    right: 0rem;
    .input-group-text {
      border-color: $input-border-color !important;
    }
  }
}
