.affixVerticalTabs {
    position: relative;
    z-index: 9;
    &:before {
        content: "";
        position: absolute;
        width: 48px;
        height: 10px;
        right: -48px;
        top: -48px;
        background-color: #1d456d;
    }
    .nav-tabs {
        display: inline-table;
        background-color: #1d456d;
        z-index: 99;
        height: calc(100vh - 48px);
        position: fixed;
        right: 0px;
        top: 48px;
        bottom: 0px;
        width: 48px;
        padding-top: 70px;
        border-bottom: none !important;
        .nav-link {
            font-size: 14px;
            font-weight: 500;
            border: none;
            color: #212529;
            padding: 0;
            position: relative;
            display: block;
            width: 44px;
            min-height: 40px;
            margin-right: 4px;
            margin: 0px 0px 3px 0px !important;
            border-radius: 0px;
            border-top-right-radius: 0.3rem;
            border-bottom-right-radius: 0.3rem;
            &:last-child {
                border: none;
                margin-right: 0rem;
            }
            &:hover,
            &:focus,
            &.active {
                background-color: #2196f2;
                border-top-right-radius: 0.3rem;
                border-bottom-right-radius: 0.3rem;
            }
        }
    }
}

